import React,{Component} from 'react';
import axios from 'axios';
class Add extends Component{
    constructor(){
        super();
        this.state={ username:"test name",userid:"test dept" }
            
    }
    getdata=(e)=>{
            this.setState({[e.target.name]: e.target.value})
        
    }  
    save=(e)=>{
            e.preventDefault();
        
            var dat={
                username:this.state.username,
                userdept:this.state.userid
            }
              axios.post("http://localhost/react/api.php",dat).then(responds=>{
                if(responds.data=="success") alert("saved");
                else alert("failed");
            })
        }      
    render(){
        return(
            <div>

<form>
          <table >
              <tr>
                  <td colspan={2}>
                      Enter Details.
                  </td>
              </tr>
              <tr><td>User Name</td><td><input onChange={this.getdata} type={"text"} name={"username"}></input></td></tr>
              <tr><td>User ID</td><td><input onChange={this.getdata} type={"text"} name={"userid"}></input></td></tr>
              <tr><td colspan={2} align={"right"}><button onClick={this.save}>Enter..</button></td></tr>
          </table>
          </form>
        <p>Entered UserName : {this.state.username} <br/>Entered userID : {this.state.userid}</p>
        </div>
       
        )
    }
}
export default Add;